import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-applied',
  templateUrl: './applied.component.html',
  styleUrls: ['./applied.component.css']
})
export class AppliedComponent implements OnInit {
  camps = []; applied=false;
  influencer: string;
  username: string;
  constructor(private service: SplashService, private router: Router) { }

  ngOnInit() {
    this.username = this.service.getUsername()
    this.influencer = this.service.getInfluencer();
    this.service.getInfluencerDetails(this.influencer).then(
      res =>{
        console.log(res);
        this.camps = res.applied
      } ,
      err => alert("Could not get influencer details")
    )
  }
  coming(){
    alert("Coming soon!! Please be patient.");
  }
  logout(){
    localStorage.clear();
    this.router.navigate(['../']);   
  }

}



