import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';
import { Router } from '@angular/router';
import {ActivatedRoute} from "@angular/router";
declare var jquery:any;
declare var $ :any;

@Component({
  selector: 'app-brand-welcome',
  templateUrl: './brand-welcome.component.html',
  styleUrls: ['./brand-welcome.component.css']
})
export class BrandWelcomeComponent implements OnInit {
  username='';
  camp = {
    name : '',
    website: '',
    email: '',
    audience: '',
    message: '',
    lastDate: '',
    imageUrl: ''

  }
  constructor(private service: SplashService,private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    let token = localStorage.getItem('token');
    if(token == null)
      this.router.navigate(['../']);
    else{
      this.service.redirect(token).then(
        res=>console.log('Authenticated'),
        err=>this.router.navigate(['../'])
      )
    }
    this.username = this.service.getUsername();
    if ( $('[type="date"]').prop('type') != 'date' ) {
      $('[type="date"]').datepicker();
  }
  }
  uploadFile(file, signedRequest, url){
    const xhr = new XMLHttpRequest();
    xhr.open('PUT', signedRequest);
    xhr.onreadystatechange = () => {
      if(xhr.readyState === 4){
        if(xhr.status === 200){
          this.camp.imageUrl=url;
          alert('Image uploading is now complete');
        }
        else{
          alert('Could not upload file.');
        }
      }
    };
    xhr.send(file);
  }
  getSignedRequest(file){
    const xhr = new XMLHttpRequest();
    xhr.open('GET', `http://splashfluence.com/camp/sign-s3?file-name=${file.name}&file-type=${file.type}`);
    xhr.onreadystatechange = () => {
      if(xhr.readyState === 4){
        if(xhr.status === 200){
          const response = JSON.parse(xhr.responseText);
          this.uploadFile(file, response.signedRequest, response.url);
        }
        else{
          alert('Could not get signed URL.');
        }
      }
    };
    xhr.send();
  }
  public fileChangeCamp(fileInput: any){
    if (fileInput.target.files[0]) 
      this.getSignedRequest(fileInput.target.files[0]);
  }
  post(){
      this.camp.lastDate=$('#date-input').val();
      console.log(this.camp);
      this.service.postCampaign(this.camp).then(
        res =>alert('camp posted successfully'),
        err=>alert('camp could not be posted'))
    }
    logout(){
      localStorage.clear();
      this.router.navigate(['']);
    }
    coming(){
      alert("Coming soon!! Please be patient");
    }
    gotoHashtag(fragment: string) {
      let url = '';
      let urlWithSegments = this.router.url.split('#');
  
      if(urlWithSegments.length){
        url = urlWithSegments[0];
      }
  
      window.location.hash = fragment;
      const element = document.querySelector("#" + fragment);
      if (element) element.scrollIntoView();
  }

}



