import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule }    from '@angular/http';

import { SplashService } from './splash.service';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { BrandLoginComponent } from './brand-login/brand-login.component';
import { BrandRegComponent } from './brand-reg/brand-reg.component';
import { InfluencerRegComponent } from './influencer-reg/influencer-reg.component';
import { BrandWelcomeComponent } from './brand-welcome/brand-welcome.component';
import { InfluencerWelcomeComponent } from './influencer-welcome/influencer-welcome.component';
import { CampComponent } from './camp/camp.component';
import { AppliedComponent } from './applied/applied.component';
import { ForgotComponent } from './forgot/forgot.component';
import { BrandUpdateComponent } from './brand-update/brand-update.component';
import { InfUpdateComponent } from './inf-update/inf-update.component';
import { AboutComponent } from './about/about.component';

const appRoutes: Routes = [
  { path: 'brandlogin', component: BrandLoginComponent},
  { path: 'infupdate', component: InfUpdateComponent},
  { path: 'live', component: CampComponent},
  { path: 'applied', component: AppliedComponent},
  { path: 'brandwelcome', component: BrandWelcomeComponent  },
  { path: 'influencerwelcome', component: InfluencerWelcomeComponent  },
  { path: 'brandreg', component: BrandRegComponent  },
  { path: 'infreg', component: InfluencerRegComponent  },
  { path: 'about', component: AboutComponent  },
  { path: 'reset/:resetToken', component: ForgotComponent  },
  { path: '**', component: HomeComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    BrandLoginComponent,
    BrandRegComponent,
    InfluencerRegComponent,
    BrandWelcomeComponent,
    InfluencerWelcomeComponent,
    CampComponent,
    AppliedComponent,
    ForgotComponent,
    BrandUpdateComponent,
    InfUpdateComponent,
    AboutComponent
  ],
  imports: [
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: false } // <-- debugging purposes only
    ),
    BrowserModule,
    HttpModule,
    FormsModule
  ],
  providers: [SplashService],
  bootstrap: [AppComponent]
})
export class AppModule { }



