import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';
import { Router } from '@angular/router';
declare var CanvasJS: any;
declare var WOW: any;
declare var jquery:any;
declare var $ :any;
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class HomeComponent implements OnInit {
    fashion = true;
    auto = false;
    travel = false;
    fitness = false;
    beauty = false;
  constructor(private router: Router, private service: SplashService) { }

  ngOnInit() {
    if($(window).width()<500){
        $('video').remove();
    }    
    let token = localStorage.getItem('token');
    if(token!=null){
        this.service.redirect(token).then(
            res=>{
                let brand  = localStorage.getItem('isBrand');
                if(brand=='true')
                    this.router.navigate(['brandwelcome']);
                else
                    this.router.navigate(['influencerwelcome']);
            }
        )
    }
    $(document).ready(function() {
        $('.graph-bar').each(function() {
           var dataWidth = $(this).data('value');
           $(this).css("width", dataWidth + "%");
        });
      });
    var chart = new CanvasJS.Chart("chartContainer",
    {
      animationEnabled: true,
        animationDuration: 2000,
        backgroundColor: "white",
        dataPointWidth: 20,
            data: [
             {
               type: "bar",
               bevelEnabled: true,
                 color: "#dc3cdc",
                 dataPoints: [
                       { y: 4.8   , label: "Newspaper Advertising"},
                         { y: 5.3   , label: "Magazine Advertising"},
                         { y: 5.4   , label: "Radio Advertising"},
                         { y: 6   , label: "Television Program Official"},
                         { y: 6.2   , label: "Television Program Embed"},
                         { y: 6.3   , label: "Online Display Advertising"},
                         { y: 6.5   , label: "Television Advertising"},
                         { y: 6.8   , label: "Celebrity Endorsement"},
                         { y: 7.2  , label: "Experiential Marketing"},
                         { y: 7.2  , label: "Influencer Marketing"}
                        ]
                       }
            ]
    });
  chart.render();
  new WOW().init();
  }
  goToLogin(x){
      this.service.setIsBrand(x);
      this.router.navigate(['brandlogin']);
  }
  myFunction() {
    var x = document.getElementById("myTopnav");
        if (x.className === "topnav")
            {
               x.className += " responsive";
              } else 
                   {
                       x.className = "topnav";
                     
   }
 }
 loadFashion(){
    this.fashion = true;
    this.auto = false;
    this.travel = false;
    this.fitness = false;
    this.beauty = false;
 }
 loadAuto(){
    this.fashion = false;
    this.auto = true;
    this.travel = false;
    this.fitness = false;
    this.beauty = false;
 }
 loadTravel(){
    this.fashion = false;
    this.auto = false;
    this.travel = true;
    this.fitness = false;
    this.beauty = false;
 }
 loadFitness(){
    this.fashion = false;
    this.auto = false;
    this.travel = false;
    this.fitness = true;
    this.beauty = false;
 }
 loadBeauty(){
    this.fashion = false;
    this.auto = false;
    this.travel = false;
    this.fitness = false;
    this.beauty = true;
 }
 
  

}



