import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import { SplashService } from '../splash.service';

@Component({
  selector: 'app-forgot',
  templateUrl: './forgot.component.html',
  styleUrls: ['./forgot.component.css']
})
export class ForgotComponent implements OnInit {
  password: string;
  confirm: string;
  token: string;
  constructor(private route: ActivatedRoute, private service: SplashService) {
    this.route.params.subscribe( params => this.token = params.resetToken );
   }

  ngOnInit() {
    
  }
  reset(){
    if(this.password==this.confirm){
      this.service.resetPassword(this.password,this.token).then(
        res=>alert("Password changed successfully"),
        err=>alert("Password might have changed or could not reset password")
      )
    }
    else
      alert("The password donot match");
      
  }

}



