import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-camp',
  templateUrl: './camp.component.html',
  styleUrls: ['./camp.component.css']
})
export class CampComponent implements OnInit {
  camps = []; applied=false;
  influencer: string;
  username: string;
  influencerDetails = {
    facebook:null,twitter:null,
    instagram:null, youtube:null,blog:null,
    location:'',category:'fashion',
    applied: []
  };
  constructor(private service: SplashService, private router: Router) { }

  ngOnInit() {
    this.service.getCampaign().then(
      res => {
        console.log(res);
        this.camps = res
      }
      ,
      err => alert("Could not retrieve campgains")
    )
    this.username = this.service.getUsername()
    this.influencer = this.service.getInfluencer();
    this.service.getInfluencerDetails(this.influencer).then(
      res => this.influencerDetails = res,
      err => alert("Could not get influencer details")
    )
  }
  check(id){
    if(this.influencerDetails.applied.indexOf(id)==-1)
      return true;
    else
      return false;
  }
  apply(id){
    this.service.putCampaign(id, this.influencer).then(
      res => {
        this.service.putInfluencer(id).then(
          res => alert("Applied successfully")
        ),
        err=> alert("Could not apply for this campaign")
      },
      err => alert("Could not apply for this campaign")
    )
  }
  logout(){
    localStorage.clear();
    this.router.navigate(['../']);
  }
  coming(){
    alert("Coming soon!! Please be patient.")
  }

}



