import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';
import { Router } from '@angular/router';
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-influencer-welcome',
  templateUrl: './influencer-welcome.component.html',
  styleUrls: ['./influencer-welcome.component.css']
})
export class InfluencerWelcomeComponent implements OnInit {
  username = '';
  contact = {name:'',email:'',message:''}
  constructor(private service: SplashService, private router: Router, private route: ActivatedRoute) { }
  
  ngOnInit() {
    let token = localStorage.getItem('token');
    if(token == null)
      this.router.navigate(['../']);
    else{
      this.service.redirect(token).then(
        res=>console.log('Authenticated'),
        err=>this.router.navigate(['../'])
      )
    }
    this.username = this.service.getUsername();
  }
  contactUs(){
    this.service.contactUs(this.contact).then(
      res=>alert("Your message was sent successfully"),
      err=>alert("Could not deliver the message")
    )
  }
  logout(){
    localStorage.clear();
    this.router.navigate(['']);
  }
  coming(){
    alert("Coming soon!! Please be patient");
  }
  gotoHashtag(fragment: string) {
    let url = '';
    let urlWithSegments = this.router.url.split('#');

    if(urlWithSegments.length){
      url = urlWithSegments[0];
    }

    window.location.hash = fragment;
    const element = document.querySelector("#" + fragment);
    if (element) element.scrollIntoView();
}

}



