import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';
@Component({
  selector: 'app-brand-update',
  templateUrl: './brand-update.component.html',
  styleUrls: ['./brand-update.component.css']
})
export class BrandUpdateComponent implements OnInit {
  token: string;
  brand = {brand_name:'', brand_url:'', username:'', password:'',
  phone:'', email:'',brand:true}
  constructor(private service: SplashService) { }

  ngOnInit() {
    this.token = localStorage.getItem('token');
    this.brand.username = localStorage.getItem('username');
  }
  postBrand(){
    console.log(this.brand);
    this.service.update(this.brand, this.token).then(
      res=> alert("Updated successfully"),
      err=> alert("Could not update user details")
    )
  }

}



