import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';
@Component({
  selector: 'app-inf-update',
  templateUrl: './inf-update.component.html',
  styleUrls: ['./inf-update.component.css']
})
export class InfUpdateComponent implements OnInit {
  token: string;
  user = {facebook:null,twitter:null,
    instagram:null, youtube:null,blog:null,
    location:'',category:'fashion', username:'', password:'',
  phone:'', email:'',brand:false}
  constructor(private service: SplashService) { }

  ngOnInit() {
    this.token = localStorage.getItem('token');
    this.user.username = localStorage.getItem('username');  
  }
  selectCategory(x){
    switch(x){
      case 1: this.user.category = "fashion"; break;
      case 2: this.user.category = "beauty"; break;
      case 3: this.user.category = "fitness"; break;
      case 4: this.user.category = "auto"; break;
      case 5: this.user.category = "travel"; break;
    }
  }
  postInfluencer(){
    console.log(this.user);
    this.service.update(this.user, this.token).then(
      res=> alert("Updated successfully"),
      err=> alert("Could not update user details")
    )
  }

}



