import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';

@Component({
  selector: 'app-brand-reg',
  templateUrl: './brand-reg.component.html',
  styleUrls: ['./brand-reg.component.css']
})
export class BrandRegComponent implements OnInit {
  brand = {brand_name:'', brand_url:'', username:'', password:'',
  phone:'', email:'',brand:true}
  constructor(private service: SplashService) { }

  ngOnInit() {
  }
  postBrand(){
    console.log(this.brand);
    this.service.postUser(this.brand).then(
      res=> alert("A verification email has been sent to your email. Please click on the link to verify.")
    )
  }

}



