import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';

@Injectable()
export class SplashService {
  private base = 'http://splashfluence.com/';
  private headers = new Headers({'Content-Type': 'application/json'});

  constructor(private http: Http) { }

  private handleError(error: any): Promise<any> {
    if(error.status==409)
      alert('User already exists');
    if(error.status==419)
      alert('Could not send verification email');
    if(error.status==429)
      alert('Please check your email for verification link');
    let token = this.headers.get('x-access-token');
    if(token!=null)
      this.headers.delete('x-access-token');
    return Promise.reject(error.message || error);
  }
  setIsBrand(x){
    this.store('isBrand',x);
  }
  getIsBrand(){
    return this.retrieve('isBrand');
  }
  setToken(x){
    this.store('token',x);
  }
  setBrand(x){
    this.store('brand',x);
  }
  getBrand(){
    return this.retrieve('brand');
  }
  setInfluencer(x){
    this.store('influencer',x);
  }
  getInfluencer(){
    return this.retrieve('influencer');
  }
  setUsername(x){
    this.store('username',x);
  }
  getUsername(){
    return this.retrieve('username');
  }
  getToken(){
    return this.retrieve('token');
  }
  private store(key,content){
    localStorage.setItem(key,content);
  }
  private retrieve(key){
    return localStorage.getItem(key);
  }
  postUser(userDetails: any): Promise<any>{
    return this.http
      .post(this.base + 'users/register', userDetails, {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  login(user: any): Promise<any> {
    return this.http
      .post(this.base + 'users/login', user, {headers: this.headers})
      .toPromise()
      .then(res =>res.json())
      .catch(this.handleError);
  }
  postCampaign(camp: any): Promise<any>{
    return this.http
      .post(this.base + 'camp', camp, {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  resetPassword(password: string, token: string): Promise<any>{
    return this.http
      .post(this.base + 'users/resetpass/' + token, {password}, {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  putCampaign(campId: string, influencerId: string): Promise<any>{
    return this.http
      .put(this.base + 'camp/' + campId ,{influencerId}, {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  putInfluencer(campId): Promise<any>{
    return this.http
      .put(this.base + 'influencer/' + this.getInfluencer(),{campId}, {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  getInfluencerDetails(influencerId): Promise<any>{
    return this.http
      .get(this.base + 'influencer/' +influencerId , {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  getCampaign(): Promise<any>{
    return this.http
      .get(this.base + 'camp', {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  addInfluncer(influencer: any): Promise<any>{
    return this.http
      .post(this.base + 'camp/' + influencer.id, {}, {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  contactUs(contact:any): Promise<any>{
    return this.http
    .post(this.base + 'users/contact', contact, {headers: this.headers})
    .toPromise()
    .then(res => res.json())
    .catch(this.handleError);  
  }
  update(update:any, token: string): Promise<any>{
    this.headers.append('x-access-token', token);
    return this.http
    .post(this.base + 'users/update', update , {headers: this.headers})
    .toPromise()
    .then(res => this.headers.delete('x-access-token'))
    .catch(this.handleError);  
  }
  redirect(token:string): Promise<any>{
    this.headers.append('x-access-token', token);
    return this.http
    .get(this.base + 'users/redirect', {headers: this.headers})
    .toPromise()
    .then(res => this.headers.delete('x-access-token'))
    .catch(this.handleError);    
  }
  forgot(email:any): Promise<any>{
    return this.http
    .post(this.base + 'users/forgot',email, {headers: this.headers})
    .toPromise()
    .then(res => res.json())
    .catch(this.handleError);     
  }
}



