import { Component, OnInit } from '@angular/core';
import { SplashService } from '../splash.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-brand-login',
  templateUrl: './brand-login.component.html',
  styleUrls: ['./brand-login.component.css']
})
export class BrandLoginComponent implements OnInit {
  brand = {username:'', password:''}
  constructor(private service: SplashService, private router: Router) { }

  ngOnInit() {
    this.brand.username = this.service.getUsername();
  }
  login(){
    this.service.login(this.brand).then(
      res=>{
        this.service.setUsername(res.username);
        this.service.setToken(res.token);
        this.service.setIsBrand(res.brand);
        if(res.brand){
          this.service.setBrand(res.brandRef);
          this.router.navigate(['brandwelcome']);
        }
        else{
          this.service.setInfluencer(res.influencerRef)
          this.router.navigate(['influencerwelcome']);
        }
      },
      err=>alert('Login details are incorrect')
    )
  }
  forgot() {
    var txt;
    var email = prompt("Please enter your email:", "email");
    if (email == null || email == "") {
        alert("Please enter a valid email address");
    } else {
      this.service.forgot({email}).then(
        res=>alert("An email was sent successfully"),
        err=>alert("This email doesnt exist in our database")
      )    
    }
  }
  goToSignup(){
    let x = this.service.getIsBrand().toString();
    if(x=="true"){
      this.router.navigate(['brandreg']);
    }
    else{
      this.router.navigate(['infreg']);
    }
  }
}



